import {mapState, mapMutations} from 'vuex';
import uniGrid from "@/components/uni-grid/uni-grid.vue";
import uniGridItem from "@/components/uni-grid/uni-grid-item.vue";
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";

export default {
	components: {
		uniGrid,
		uniGridItem,
		uniLoadMore,
		empty
	},
	data() {
		return {
			carouselList: [],
			swiperCurrent: 0,
			swiperLength: 0,
			
			tabCurrentIndex: 0,
			navList: [
				{
					id: 2,
					state: 'views',
					text: '最热曲谱',
					loadingType: 'more',
					musicList: []
				},
				{
					index: 3,
					state: 'created_at',
					text: '最新曲谱',
					loadingType: 'more',
					musicList: []
				},
				{
					index: 5,
					state: 'views',
					text: '冷门曲谱',
					loadingType: 'more',
					musicList: []
				},
			],
			
			cateList: [{
					cateid: 1,
					url: 'https://img-cdn-qiniu.dcloud.net.cn/uni-ui/grid-1.png',
					text: '新浪'
				},
				{
					cateid: 2,
					url: 'https://img-cdn-qiniu.dcloud.net.cn/uni-ui/grid-2.png',
					text: '微信'
				},
				{
					cateid: 3,
					url: 'https://img-cdn-qiniu.dcloud.net.cn/uni-ui/gird-3.png',
					text: 'QQ'
				},
				{
					cateid: 4,
					url: 'https://img-cdn-qiniu.dcloud.net.cn/uni-ui/grid-6.png',
					text: '抖音'
				},
				{
					cateid: 5,
					url: 'https://img-cdn-qiniu.dcloud.net.cn/uni-ui/grid-7.png',
					text: '百度'
				},
				{
					cateid: 6,
					url: 'https://img-cdn-qiniu.dcloud.net.cn/uni-ui/grid-8.png',
					text: '支付宝'
				},
				{
					cateid: 7,
					url: 'https://img-cdn-qiniu.dcloud.net.cn/uni-ui/grid-9.png',
					text: '字节跳动'
				},
				{
					cateid: 8,
					url: 'https://img-cdn-qiniu.dcloud.net.cn/uni-ui/grid-4.png',
					text: '手机'
				}
			]
		}
	},
	onLoad() {
		/* 首页轮播图数据 */
		this.carouselList = [{
				id: 2,
				src: "/static/swiper/1.jpg"
			},
			{
				id: 3,
				src: "/static/swiper/2.jpg",
			},
			{
				id: 5,
				src: "/static/swiper/3.jpg"
			}
		];
		this.swiperLength = this.carouselList.length;
		
		this.tabCurrentIndex = 0;
		this.loadMusicData('tabChange')
	},
	computed: {
	},
	methods: {
		...mapMutations(['logout']),
		gotoinfo: function(e) {
			var newsid = e.currentTarget.dataset.newsid;
			uni.navigateTo({
				url: '../info/info?newsid=' + newsid,
				success: res => {
					console.log('gotoinfo success')
				},
				fail: () => {
					console.log('gotoinfo fail')
				},
				complete: () => {
					console.log('gotoinfo complete')
				}
			});
		},
		tologout: function() {
			// 退出登录后不需要显示的刷新页面，因为退出登录成功后 state改变 页面会自动重新加载
			// 这是遵循vue的state状态机制的
			this.logout();
		},
		
		clickGrid: function(e) {
			var index = e.detail.index;
			uni.navigateTo({
				url: "/pages/info/info?newsid=" + this.cateList[index].cateid
			});
		},
		
		swiperChange: function(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
		},
		navToDetailPage: function(item) {
			console.log(item)
			let id = item.id;
			uni.navigateTo({
				url: `/pages/info/info?newsid=${id}`
			})
		},
		
		async loadMusicData(source){
			// 这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			
			if(source === 'tabChange' && navItem.loaded === true){
				//tab切换只有第一次需要加载数据
				return;
			}
			if(navItem.loadingType === 'loading' || navItem.loadingType === 'noMore'){
				//防止重复加载
				return;
			}
			
			navItem.loadingType = 'loading';
	
			let paras = {
				page: 1,
				pagesize: 10,
				name: '',
				type: '',
				level: '',
				order: state
			}
			let data = await this.$apis.getMusicList(paras);
			console.log(data)
			
			data.data.forEach(item => {
				navItem.musicList.push(item);
			})
			
			
			// this.$http.request({
			// 	url: '/music/getMusicList',
			// 	method: 'post',
			// 	header: {},
			// 	params: 
			// }).then(res => {
			// 	console.log(res)
			// 	var musicList = res.data.data.data
				
			// 	musicList.forEach(item => {
			// 		navItem.musicList.push(item);
			// 	})
			// }).catch(err => {
			// 	console.log(err)
			// })
			
			//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
			this.$set(navItem, 'loaded', true);
			
			//判断是否还有数据， 有改为 more， 没有改为noMore 
			// navItem.loadingType = 'more';
			navItem.loadingType = 'noMore';
			
		}, 
		//swiper 切换
		changeTab(e){
			this.tabCurrentIndex = e.target.current;
			this.loadMusicData('tabChange');
		},
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
		}
	}
}
